import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    ARN: '',
    count: 0
  },
  mutations: {
    secuencia (state) {
      this.state.count = 0
      var array = []
      var array2 = []
      var array3 = []
      var i = 0
      for (i = 0; i < state.ARN.length - 2; i++) {
        array[i] = state.ARN[i].toUpperCase().concat(state.ARN[i + 1].toUpperCase()).concat(state.ARN[i + 2].toUpperCase())
        i += 2
      };
      array2 = array.filter(Boolean)
      for (i = 0; i < array2.length - 1; i++) {
        array3[i] = array2[i].toUpperCase().concat(array2[i + 1].toUpperCase())
      }
      for (i = 0; i < array3.length; i++) {
        if (array3[i] === 'TAAAAA') {
          this.state.count = 2
        };
        if (array3[i] === 'AAAAAT') {
          this.state.count = 2
        };
        if (array3[i] === 'TATATA') {
          this.state.count = 2
        };
        if (array3[i] === 'ATATAT') {
          this.state.count = 2
        };
      }
      this.state.ARN = ''
    }
  },
  actions: {
  },
  modules: {
  }
})
